* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Urbanist";
  font-size: 16px;
}

ol, ul {
  list-style: none;
}

a {
  display: inline-block;
  text-decoration: none;
  transition: all 0.3s ease;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
}

input:focus, textarea:focus {
  outline: 0;
}

