.site-header {
  position: absolute;
  width: 100%;
  padding: 25px 0;
  z-index: 3;
}
.site-header__wrapper {
  display: flex;
  justify-content: space-between;
}
.site-header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.site-header__logo {
  min-width: 80px;
}
.site-header__nav {
  transition: all 0.3s ease;
}
@media screen and (max-width: 1200px) {
  .site-header__nav {
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    width: 100%;
    transform: translateX(-200%);
    background-color: #050835;
  }
  .site-header__nav .site-header__ul {
    padding-top: 20%;
    flex-direction: column;
    align-items: center;
  }
  .site-header__nav .site-header__ul a {
    color: #fff;
  }
}
.site-header__ul {
  display: flex;
  justify-content: space-between;
}
.site-header__ul a {
  padding: 10px 14px;
  font-weight: 500;
  color: #2d2e33;
}
.site-header__form-wrapper {
  padding: 0 30px;
  margin: 0 30px 0 15px;
  border-left: 1px solid #cbccce;
  border-right: 1px solid #cbccce;
}
.site-header__form {
  padding: 5px 20px 5px 5px;
  border-radius: 50px;
  background-color: #fff;
}
@media screen and (max-width: 576px) {
  .site-header__form {
    display: none;
  }
}
@media screen and (max-width: 1360px) {
  .site-header__form {
    padding: 0;
  }
  .site-header__form span {
    display: none;
  }
}
.site-header__btn {
  padding: 10.5px 31px;
  color: #fff;
  background-color: #050835;
}
.site-header__language-switcher {
  font-weight: 500;
  border: 0;
  text-transform: uppercase;
  color: #2d2e33;
  background-color: transparent;
}

.show-menu {
  transform: translateX(0);
}

.burger {
  margin-left: 23px;
  z-index: 10;
  cursor: pointer;
}
@media screen and (min-width: 1200px) {
  .burger {
    display: none;
  }
}

.bar1, .bar2, .bar3 {
  width: 25px;
  height: 4px;
  background-color: #000;
  margin: 2px 0;
  transition: 0.4s;
  border-radius: 20px;
}

.change .bar1 {
  transform: rotate(-45deg) translate(0, 1px);
  background-color: #fff;
}

.change .bar2 {
  opacity: 0;
}

.change .bar3 {
  transform: rotate(45deg) translate(-7px, -8px);
  background-color: #fff;
}

.hero {
  position: relative;
  padding: 200px 0;
  text-align: center;
  color: #fff;
  background-image: url("../../../assets/img/hero.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.hero::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  mix-blend-mode: multiply;
  background: transparent -webkit-gradient(linear, left top, left bottom, from(#ffd2ac), to(rgba(144, 70, 7, 0.8666666667))) 0% 0% no-repeat padding-box;
}
.hero .l-container {
  position: relative;
  z-index: 2;
}
.hero .u-h1 {
  margin-bottom: 7px;
}
.hero p {
  font-weight: 500;
}
@media screen and (min-width: 576px) {
  .hero p {
    font-size: 20px;
  }
}
.hero__form {
  max-width: 799px;
  margin: 76px auto 20px;
  padding: 5px;
}
@media screen and (min-width: 768px) {
  .hero__form {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    border-radius: 50px;
    background-color: #fff;
  }
}
.hero__search {
  position: relative;
  flex: 1.1;
  padding: 6px 20px 6px 48px;
  background-image: url(../../../assets/img/svg/feather-search.svg);
  background-repeat: no-repeat;
  background-position: 21px 50%;
}
.hero__search input {
  width: 100%;
  border: 0;
}
.hero__search input::placeholder {
  color: #000;
}
@media screen and (max-width: 768px) {
  .hero__search {
    margin-bottom: 15px;
    padding: 10px 10px 10px 48px;
    border-radius: 50px;
    background-color: #fff;
  }
}
.hero__select-wrapper {
  position: relative;
  flex: 0.9;
  margin-right: 20px;
  padding: 5px 20px 5px 48px;
  border-left: 1px solid #cbccce;
  border-right: 1px solid #cbccce;
  background-image: url(../../../assets/img/svg/portfolio.svg);
  background-repeat: no-repeat;
  background-position: 21px 50%;
}
@media screen and (max-width: 768px) {
  .hero__select-wrapper {
    margin-right: 0;
    margin-bottom: 15px;
    padding: 10px 10px 10px 48px;
    border-radius: 50px;
    background-color: #fff;
  }
}
.hero__select {
  width: 100%;
  border: 0;
}
.hero__btn {
  padding: 15.5px 31px;
  border: 0;
  border-radius: 50px;
  color: #fff;
  background: #050835;
}
.hero__arrow {
  position: absolute;
  z-index: 1;
}
@media screen and (max-width: 576px) {
  .hero__arrow {
    display: none;
  }
}
.hero__arrow--top {
  top: 142px;
  right: 64px;
}
.hero__arrow--bottom {
  left: 64px;
  bottom: 61px;
}
@media screen and (min-width: 576px) {
  .hero {
    padding: 235px 0 407px;
  }
}

.links-wrapper {
  margin: 40px 0 10px;
}
@media screen and (min-width: 768px) {
  .links-wrapper {
    margin: 80px 0 24px;
  }
}

.links {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.links__single {
  display: flex;
  align-items: center;
  width: 340px;
  margin-bottom: 30px;
}
@media screen and (min-width: 768px) {
  .links__single {
    margin-bottom: 56px;
  }
}
.links__svg {
  width: 78px;
  height: 78px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
  border-radius: 50%;
}
.links p {
  margin-top: 5px;
  color: #8b8b8e;
}

.l-container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 15px;
}

.l-tablet-show {
  display: block;
}
@media screen and (min-width: 768px) {
  .l-tablet-show {
    display: none;
  }
}

.l-tablet-hide {
  display: none;
}
@media screen and (min-width: 768px) {
  .l-tablet-hide {
    display: block;
  }
}

.u-h1 {
  font-size: 40px;
  font-weight: 600;
}
@media screen and (min-width: 768px) {
  .u-h1 {
    font-size: 50px;
  }
}
@media screen and (min-width: 1200px) {
  .u-h1 {
    font-size: 70px;
  }
}

.u-h2-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}
.u-h2-wrapper img {
  margin-right: 20px;
}

.u-h2 {
  font-size: 30px;
  color: #2d2e33;
}
@media screen and (min-width: 768px) {
  .u-h2 {
    font-size: 40px;
  }
}

.u-h4 {
  font-size: 20px;
  font-weight: bold;
}

.u-btn {
  border: 0;
  border-radius: 50px;
}

.u-stop-scroll {
  overflow: hidden;
}

